@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
* {
  font-family: "Poppins", sans-serif;
}
main {
  margin-top: 2em;
  margin-bottom: 2em;
  margin-left: 2em;
  min-height: 25em;
}

.site-title {
  font-family: "Lobster", cursive;
}
.links {
  text-decoration: none !important;
  color: inherit !important;
}
.links:hover {
  cursor: pointer;
  text-decoration: underline !important;
}

.singlepost {
  border: 1px solid black;
  margin-bottom: 2em;
  border-radius: 5px;
  box-shadow: 0px 2px 3px 0px rgb(220 128 121 / 47%);
  padding-bottom: 1em;
}
.chat:hover {
  cursor: pointer;
}

@media (max-width: 850px) {
  .postCont {
    flex-direction: column;
  }
}

.posts {
  width: 70%;
}

@media (max-width: 850px) {
  .mainCont {
    flex-direction: column;
  }
  .posts {
    width: 70%;
  }
}

.posts {
  width: 70%;
}

.requestbox {
  padding: 1em;
  width: 70%;
  border: 1px solid black;
  margin-bottom: 2em;
  border-radius: 5px;
  box-shadow: 0px 2px 3px 0px rgb(220 128 121 / 47%);
  padding-bottom: 1em;
}

